import { HttpClient, HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { AndroidFullScreen } from '@ionic-native/android-full-screen';
import { File } from '@ionic-native/file';

import { Utils, Data, Stimuli, TrainingProvider, PairComparisonProvider, 
  OutputEstimationProvider, RankingTaskProvider} from '../providers/providers';
import { MyApp } from './app.component';

// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

/*export function provideSettings(storage: Storage) {
 
  return new Settings(storage, {
    option1: true,
    option2: 'Ionitron J. Framework',
    option3: '3',
    option4: 'Hello'
  });
}*/

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp, {
      preloadModules: true
    }, 
{
  links: [
    { loadChildren: '../pages/output-estimation-instructions/output-estimation-instructions.module.ngfactory#OutputEstimationInstructionsPageModuleNgFactory', name: 'OutputEstimationInstructionsPage', segment: 'output-estimation-instructions', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/output-estimation/output-estimation.module.ngfactory#OutputEstimationPageModuleNgFactory', name: 'OutputEstimationPage', segment: 'output-estimation', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/pair-comparison-instructions/pair-comparison-instructions.module.ngfactory#PairComparisonInstructionsPageModuleNgFactory', name: 'PairComparisonInstructionsPage', segment: 'pair-comparison-instructions', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/pair-comparison/pair-comparison.module.ngfactory#PairComparisonPageModuleNgFactory', name: 'PairComparisonPage', segment: 'pair-comparison', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/training-instructions/training-instructions.module.ngfactory#TrainingInstructionsPageModuleNgFactory', name: 'TrainingInstructionsPage', segment: 'training-instructions', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/reward/reward.module.ngfactory#RewardPageModuleNgFactory', name: 'RewardPage', segment: 'reward', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/training-instructions2/training-instructions2.module.ngfactory#TrainingInstructions2PageModuleNgFactory', name: 'TrainingInstructions2Page', segment: 'training-instructions2', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/training/training.module.ngfactory#TrainingPageModuleNgFactory', name: 'TrainingPage', segment: 'training', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/registration/registration.module.ngfactory#RegistrationPageModuleNgFactory', name: 'RegistrationPage', segment: 'registration', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/ranking-task/ranking-task.module.ngfactory#RankingTaskPageModuleNgFactory', name: 'RankingTaskPage', segment: 'ranking-task', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot({
      name: 'data',
      driverOrder: ['sqlite', 'indexeddb', 'websql']
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    SplashScreen,
    StatusBar,
    AndroidFullScreen,
    /*{ provide: Settings, useFactory: provideSettings, deps: [Storage] },*/
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    File,
    Utils,
    Data,
    Stimuli,
    TrainingProvider,
    PairComparisonProvider,
    OutputEstimationProvider,
    RankingTaskProvider
  ]
})
export class AppModule { }



